<script>
import logo from "@/assets/logos/girllogo.png";
import { Carousel, Slide } from "vue-carousel";
import axios from "axios";
import Eventcard from "./eventCard";
import SlideCard from "./slideCard";

export default {
  components: {
    Carousel,
    Slide,
    Eventcard,
    SlideCard,
  },
  data() {
    return {
      window: {
        width: 0,
        height: 0,
      },
      perPage: 3,
      logo: logo,
      events: [],
      teamSlides: [],
      slides: [
        {
          name:
            'Welcome to  <span class="slideBolder">Girlscript</span> Coimbatore',
          image:
            "https://images.unsplash.com/photo-1504384308090-c894fdcc538d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80",
        },
      ],
    };
  },
  mounted() {
    document.title = "Girlscript | Home";
    this.handleResize();
    this.loadall();
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    loadall() {
      let apiurl = "https://sethu-1471.github.io/girlscript_api/event.json";
      axios.get(apiurl).then((res) => {
        console.log(res);
        if (res.data.event) {
          this.loading = false;
          res.data.event.forEach((e) => {
            this.events.push(e);
          });
        }
      });
      let apiurlone = "https://sethu-1471.github.io/girlscript_api/slides.json";
      axios.get(apiurlone).then((res) => {
        console.log(res);
        if (res.data.slides) {
          res.data.slides.forEach((e) => {
            this.teamSlides.push(e);
          });
        }
      });
    },
    handleResize() {
      this.window.width = window.innerWidth;
      this.window.height = window.innerHeight;
      if (this.window.width <= 750) {
        console.log(this.window.height);
        this.perPage = 1;
      } else if (this.window.width <= 1000) {
        this.perPage = 2;
      } else {
        this.perPage = 3;
      }
    },
  },
};
</script>

